}

.header-btn-box {
  align-items: center;
  gap: 14px;
  display: flex;
}

.input-switcher[type="checkbox"] {
  visibility: hidden;
  width: 0;
  height: 0;
  display: none;
}

.input-label {
  cursor: pointer;
  text-indent: -9999px;
  background: #cecdcd;
  border-radius: 18px;
  width: 46px;
  height: 20px;
  display: block;
  position: relative;
}

.input-label:after {
  content: "";
  background: var(--white_2);
  border-radius: 18px;
  width: 18px;
  height: 18px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.input-switcher:checked + .input-label {
  background: #9bb537;
}

.input-switcher:checked + .input-label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.input-label:active:after {
  width: 26px;
}

.menu-toggle {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  min-height: 40px;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: #0000001a;
}

.menu-container {
  opacity: 0;
  visibility: hidden;
  background-color: var(--bg-mob-menu);
  z-index: 999;
  width: 40%;
  min-width: 220px;
  height: 100%;
  padding-top: 200px;
  transition: opacity .25s ease-in-out, visibility .25s ease-in-out, transform .25s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: auto;
  transform: translateX(-100%);
}

.menu-container.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}

.block {
  overflow: hidden;
}

.menu-container .menu-toggle {
  color: var(--white_2);
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mobile-menu .nav-link {
  color: var(--white_2);
  text-align: center;
  padding: 0 50px 16px;
  text-decoration: none;
  display: block;
}

.nav-link {
  color: var(--txt-color);
  font-size: 12px;
  line-height: 1.25;
}

.mobile-menu .current-mob {
  color: var(--current-mob);
}

.theme-switcher.burger {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

